import axios from "axios"

// Action types
const GET = 'myApp/lighting/GET';
const ADD = 'myApp/lighting/ADD';
const LOCAL_ADD = 'myApp/lighting/LOCAL_ADD';
const LOCAL_REMOVE = 'myApp/lighting/LOCAL_REMOVE';


const initialState = {
  lightingList: [],
  localLightingList: [],
  isLoading: false,
  isError: false
}

// Reducer
export default function reducer(state = initialState, action = {}) {
  switch (action.type) {
    case `${GET}_PENDING`:
      return {
        ...state,
        isLoading: true
      };
    case `${GET}_FULFILLED`:
      return {
        ...state,
        lightingList: [...action.payload],
        isLoading: false,
      };
    case `${GET}_REJECT`:
      return {
        ...state,
        isLoading: false,
        isError: true
      };

    case `${ADD}_PENDING`:
      return {
        ...state,
        isLoading: true
      };
    case `${ADD}_FULFILLED`:
      return {
        ...state,
        isLoading: false,
      };
    case `${ADD}_REJECT`:
      return {
        ...state,
        isLoading: false,
        isError: true
      };

    case LOCAL_ADD:
      return {
        ...state,
        localLightingList: [...state.localLightingList, action.payload],
      };
    case LOCAL_REMOVE:
      return {
        ...state,
        localLightingList: [
          ...state.localLightingList.slice(0, action.payload),
          ...state.localLightingList.slice(action.payload + 1)
        ]
      };

    default:
      return state;
  }
}

// actions
export const addLighting = (lightingList) => dispatch => dispatch({
  type: ADD,
  payload: axios.post("", lightingList)
});

export const getLighting = () => dispatch => dispatch({
  type: GET,
  payload: axios.get("").then(res => res.data)
});

export const addLocalLightingList = (localLighting) => ({
  type: LOCAL_ADD,
  payload: localLighting
});

export const removeLocalLightingList = (index) => ({
  type: LOCAL_REMOVE,
  payload: index
});
