import React, {Component, Fragment} from 'react';
import {List, Button, Col} from 'antd';


class LightingList extends Component {

  handleRemove = index => {
    console.log(index)
    this.props.handleRemoveLighting(index)
  }

  render() {
    return (
      <Fragment>
        <List
          bordered
          dataSource={this.props.localLightingList}
          renderItem={(item, i) => (<List.Item>
            <Col span={12}> {item.subject} </Col>
            <Col span={4}> {item.duration} min</Col>
            <Col span={4} offset={0}>
              <Button onClick={e => this.handleRemove(i)}>Delete</Button>
            </Col>
          </List.Item>)}
        />
      </Fragment>
    );
  }
}

export default LightingList;
