import React, {Component, Fragment} from 'react';
import {connect} from 'react-redux';
import {Row, Col} from 'antd';

import LightingForm from "../components/LightingForm";
import LightingList from "../components/LightingList";
import LightingDetail from "../components/LightingDetail";

import {addLighting, getLighting, addLocalLightingList,removeLocalLightingList} from '../redux/lighting'

class Home extends Component {

  handleAddLighting = lighting => {
    this.props.addLocalLightingList(lighting)
  };

  handleRemoveLighting = index => {
    this.props.removeLocalLightingList(index)
  };

  render() {
    return (
      <Fragment>
        <Row>
          <Col span={12}>
            <LightingForm handleAddLighting={this.handleAddLighting}/>
          </Col>

          <Col span={12}>
            <LightingList handleRemoveLighting={this.handleRemoveLighting}
                          localLightingList={this.props.localLightingList}/>
          </Col>
        </Row>

        <LightingDetail lightingList={this.props.lightingList}/>
      </Fragment>
    );
  }
}

const mapStateToProps = ({lighting}) => ({
  lightingList: lighting.lightingList,
  localLightingList: lighting.localLightingList
});

const mapDispatchToProps = {
  addLighting,
  getLighting,
  addLocalLightingList,
  removeLocalLightingList
}

export default connect(mapStateToProps, mapDispatchToProps)(Home);
